import { Component, OnInit } from '@angular/core';
import { SharedService } from '../sharedservice/taskss.service';
import { AppService} from '../../Services/app.service';
import { Task } from '../../model/Task';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
import {Observable} from 'rxjs';
import {debounceTime, map} from 'rxjs/operators';
import { FormsModule } from '@angular/forms';




@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css']
})
export class ViewComponent implements OnInit {
  tasks:Task[];
  searchText:string;
  searchParent:string;
  mydate;
  searchPrioFrom:number;
  searchPrioTo:number;
  parents:{_id:string, task:string}[];
  selectedParent:{_id:string, task:string} = null;
  search:any;
  formatter:any;
  
  //create a object to store the selected task
   constructor(private appService:AppService,private taskService:SharedService,private router: Router,public datepipe: DatePipe) { 
    this.taskService.TaskModel = new Task();
   }

  ngOnInit() {
    this.ngBootstrapTypeahead()
    this.findAllTasks()

    this.searchText='';
    this.searchParent='';
  }

  editTask(task1:Task)
  {
    //this.taskService.TaskModel.task=task1.task;
    this.taskService.TaskModel = task1;
    this.router.navigate(['/', 'add']);
  }
  endTask(task1:Task)
  {
    task1.status=true;
    this.appService.updateTask(task1)
    .subscribe((res: any)=>{
      // console.log(res);
      if(res.success == true){
        // console.log('valid')
        this.router.navigate(['/', 'view task']);
      } else {
        alert('Invalid user');
      }
    })

  }

  ngBootstrapTypeahead(){
    this.search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      map(term => term === '' ? []
      : this.parents.filter(parent => parent.task.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10))
    );
    
    this.formatter = (x: {task: string}) => {
      // // console.log(x);
      return x.task;
    };  
    
  }

  findAllTasks(){
    this.appService.getTask()
    .subscribe((res: any)=>{
      console.log(res);
      if(res.success == true){
        this.tasks = res.data;
        this.parents = res.data;
      } else {
        console.log("Could not find all tasks");
      }
    })
  }

  dateconversion(x:any){
    this.mydate= new Date(x);
    let latest_date = this.datepipe.transform(this.mydate,'yyyy-MM-dd');
    return latest_date;
  }


}
