import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UtilService } from '../util/util.service';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  constructor(private http: HttpClient, private util: UtilService) { 
    // console.log(this.util.host);
  }

 
  // Add User
  addUser(user) {
    return this.http.post('${this.util.host}/api/user/addUser/', user);
  }

  // Get User
  getUser() {
    return this.http.get('${this.util.host}/api/user/getUser/');
  }

  // Update User
  updateUser(user) {
    return this.http.post('${this.util.host}/api/user/updateUser/', user);
  }

  // Delete User
  deleteUser(user) {
    return this.http.post('${this.util.host}/api/user/deleteUser/', user);
  }

  // Add Project
  addProject(project) {
    return this.http.post('${this.util.host}/api/project/addProject/', project);
  }

  // Get Project
  getProject() {
    return this.http.get('${this.util.host}/api/project/getProject/');
  }

  // Update Project
  updateProject(project) {
    return this.http.post('${this.util.host}/api/project/updateProject/', project);
  }

  // Delete Project
  deleteProject(project) {
    return this.http.post('${this.util.host}/api/project/deleteProject/', project);
  }

  // Add Task
  addTask(task) {
    return this.http.post('${this.util.host}/api/task/addTask/', task);
  }

  // Get Task
  getTask() {
    return this.http.get('${this.util.host}/api/task/getTask/');
  }

  // Update Task
  updateTask(task) {
    return this.http.post('${this.util.host}/api/task/updateTask/', task);
  }

  // Delete Task
  deleteTask(task) {
    return this.http.post('${this.util.host}/api/task/deleteTask/', task);
  }
}
