import { Component, OnInit } from '@angular/core';
import { User } from '../model/User';
import { AppService} from '../Services/app.service'
import { Router } from '@angular/router';
import {Observable} from 'rxjs';
import {debounceTime, map} from 'rxjs/operators';
import { isNullOrUndefined, isUndefined } from 'util';

@Component({
  selector: 'app-add-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  user: User = new User();
  userData: any;
  addupdbutton: string;
  userId: number;
  addevent: boolean;
  errmsg: any;
  dataavailable : boolean;

  constructor(private userService: AppService, private router: Router) { }

  ngOnInit() {
    //this.ngBootstrapTypeahead();
    // find all the parent ids and tasks
    this.findAllUsers();
    this.addevent = true; //Assume the form is loaded for add event.
    this.addupdbutton= "Add";
  }
  
  findAllUsers(){
    this.userService.getUser()
    .subscribe((res: any)=>{
      // console.log(res);
      if(res.success == true){
        this.userData = res.data;
        if (this.userData.length > 0){
          this.dataavailable= true;
        }

        
      } else {
        // console.log("error in finding all tasks");
      }
    })
  }

  
  clearerrmsg(){
    this.errmsg='';
  }
  resetUser(){
   this.clearerrmsg();
   this.user = new User();
  }
  validateTask(){
    
    return true;
  }

  addUser(){
    if (this.addevent){
    this.userService.addUser(this.user)
    .subscribe((res: any)=>{
      if(res.success == true){
        this.findAllUsers();
        this.resetUser();
      } else {
        alert('Invalid user');
      }
    })
  }
  else{
    //code for update starts.
    this.userService.updateUser(this.user)
    .subscribe((res: any)=>{
      if(res.success == true){
        this.findAllUsers();
        this.resetUser();
      } else {
        alert('Invalid user');
      }
    })
  }

  }
 
  editUser(user) {
    //this.userId = user._id;
    this.addevent = false;
    this.addupdbutton= "Update";
    this.displayuser(user);
  }
  deleteUser(user){
    this.userService.deleteUser(this.user._id)
    .subscribe((res: any)=>{
      if(res.success == true){
        this.findAllUsers();
      } else {
        alert('Invalid user');
      }
    })
  }
  displayuser(userdetails){
    this.user.firstName = userdetails.firstName;
    this.user.lastName = userdetails.lastName;
    this.user.empId = userdetails.empId;
  }
}
